import React from 'react';

// material-ui
import { makeStyles } from '@material-ui/core/styles';
import {
	Box,
	Button,
	FormControl,
	FormHelperText,
	Grid,
	IconButton,
	InputAdornment,
	InputLabel,
	OutlinedInput,
	Typography,
} from '@material-ui/core';

// third party
import * as yup from 'yup';
import { Formik, FormikHelpers } from 'formik';
import _get from 'lodash.get';

// project imports
import { Visibility, VisibilityOff } from '@material-ui/icons';
import { Link, useHistory, useParams } from 'react-router-dom';
import { useAuth, useSnackBar } from '../../../../hooks';
import AnimateButton from '../../../../ui-component/extended/AnimateButton';
import {
	strengthColor,
	strengthIndicator,
	cognitoAwsPasswordRegExp,
} from '../../../../utils/password-strength';
import { validationErrors, helperText } from '../constants';

// style constant
const useStyles = makeStyles(theme => ({
	loginInput: {
		...theme.typography.customInput,
	},
	notchedOutline: {
		borderRadius: `${theme?.customization?.borderRadius}px`,
	},
}));

type ConfirmationPasswordModel = {
	otpCode: string;
	password: string;
	confirmPassword: string;
	submit: boolean;
};

const ConfirmationPasswordForm = ({ ...others }) => {
	const history = useHistory();
	const classes = useStyles();
	const showSnackBar = useSnackBar();
	const { username } = useParams<{ username: string }>();

	const [showPassword, setShowPassword] = React.useState(false);
	const [strength, setStrength] = React.useState(0);
	const [level, setLevel] = React.useState<
		{ label: string; color: string } | undefined
	>(undefined);

	const { confirmPassword } = useAuth();

	const handleClickShowPassword = () => {
		setShowPassword(!showPassword);
	};

	const changePasswordColorLevel = (value: string) => {
		const temp = strengthIndicator(value);
		setStrength(temp);
		setLevel(strengthColor(temp) as any);
	};

	const confirmationSchema = yup.object().shape({
		otpCode: yup.string().max(6).required(validationErrors.requiredOtpCode),
		password: yup
			.string()
			.max(255)
			.required(validationErrors.requiredPassword)
			.matches(cognitoAwsPasswordRegExp, validationErrors.validPassword),
		confirmPassword: yup
			.string()
			.max(255)
			.required(validationErrors.requiredConfirmPassword)
			.oneOf([yup.ref('password'), null], validationErrors.matchedPasswords),
	});

	const submitConfirmation = async (
		values: ConfirmationPasswordModel,
		{ setStatus, setSubmitting }: FormikHelpers<ConfirmationPasswordModel>
	) => {
		try {
			await confirmPassword(username, values.otpCode, values.password);
			history.push('/login');
		} catch (err) {
			setStatus({ success: false });
			setSubmitting(false);
			showSnackBar({
				message: _get(err, 'message', 'Something went wrong'),
				alertSeverity: 'error',
			});
		}
	};

	return (
		<Formik
			initialValues={{
				otpCode: '',
				password: '',
				confirmPassword: '',
				submit: false,
			}}
			validationSchema={confirmationSchema}
			onSubmit={submitConfirmation}
		>
			{({
				errors,
				handleBlur,
				handleChange,
				handleSubmit,
				isSubmitting,
				touched,
				values,
			}) => (
				<form noValidate onSubmit={handleSubmit} {...others}>
					<FormControl
						fullWidth
						error={Boolean(touched.otpCode && errors.otpCode)}
						className={classes.loginInput}
						variant='outlined'
					>
						<InputLabel htmlFor='outlined-adornment-otpCode-login'>
							OTP Code
						</InputLabel>
						<OutlinedInput
							id='outlined-adornment-otpCode-login'
							type='text'
							value={values.otpCode}
							name='otpCode'
							onBlur={handleBlur}
							onChange={handleChange}
							label='otpCode'
							inputProps={{}}
						/>
						{touched.otpCode && errors.otpCode && (
							<FormHelperText error id='standard-weight-helper-text-otpCode-login'>
								{errors.otpCode}
							</FormHelperText>
						)}
					</FormControl>
					<FormControl
						fullWidth
						error={Boolean(touched.password && errors.password)}
						className={classes.loginInput}
						variant='outlined'
					>
						<InputLabel htmlFor='outlined-adornment-password-reset'>
							New Password
						</InputLabel>
						<OutlinedInput
							id='outlined-adornment-password-reset'
							type={showPassword ? 'text' : 'password'}
							value={values.password}
							name='password'
							onBlur={handleBlur}
							onChange={e => {
								handleChange(e);
								changePasswordColorLevel(e.target.value);
							}}
							endAdornment={
								<InputAdornment position='end'>
									<IconButton
										aria-label='toggle password visibility'
										onClick={handleClickShowPassword}
										edge='end'
									>
										{showPassword ? <Visibility /> : <VisibilityOff />}
									</IconButton>
								</InputAdornment>
							}
							inputProps={{
								classes: {
									notchedOutline: classes.notchedOutline,
								},
							}}
						/>
					</FormControl>
					<FormControl fullWidth>
						<FormHelperText id='password-helper-text'>
							{helperText.password}
						</FormHelperText>
					</FormControl>
					{touched.password && errors.password && (
						<FormControl fullWidth>
							<FormHelperText error id='standard-weight-helper-text-reset'>
								{errors.password}
							</FormHelperText>
						</FormControl>
					)}
					{strength !== 0 && (
						<FormControl fullWidth>
							<Box mb={2}>
								<Grid container spacing={2} alignItems='center'>
									<Grid item>
										<Box width={85} height={8} borderRadius={7} bgcolor={level?.color} />
									</Grid>
									<Grid item>
										<Typography variant='subtitle1' fontSize='0.75rem'>
											{level?.label}
										</Typography>
									</Grid>
								</Grid>
							</Box>
						</FormControl>
					)}
					<FormControl
						fullWidth
						error={Boolean(touched.confirmPassword && errors.confirmPassword)}
						className={classes.loginInput}
						variant='outlined'
					>
						<InputLabel htmlFor='outlined-adornment-confirm-password'>
							Confirm New Password
						</InputLabel>
						<OutlinedInput
							id='outlined-adornment-confirm-password'
							type='password'
							value={values.confirmPassword}
							name='confirmPassword'
							label='Confirm Password'
							onBlur={handleBlur}
							onChange={handleChange}
							inputProps={{
								classes: {
									notchedOutline: classes.notchedOutline,
								},
							}}
						/>
					</FormControl>

					{touched.confirmPassword && errors.confirmPassword && (
						<FormControl fullWidth>
							<FormHelperText error id='standard-weight-helper-text-confirm-password'>
								{errors.confirmPassword}
							</FormHelperText>
						</FormControl>
					)}

					{errors.submit && (
						<Box mt={3}>
							<FormHelperText error>{errors.submit}</FormHelperText>
						</Box>
					)}
					<Box mt={1}>
						<AnimateButton>
							<Button
								disableElevation
								disabled={isSubmitting}
								fullWidth
								size='large'
								type='submit'
								variant='contained'
								color='secondary'
							>
								Update Password
							</Button>
						</AnimateButton>
					</Box>
					<Typography variant='subtitle1' component={Link} to='/login'>
						Have an account?
					</Typography>
				</form>
			)}
		</Formik>
	);
};

export default ConfirmationPasswordForm;
